<template>
    <app-bredcrumbs></app-bredcrumbs>
    <app-blbooks></app-blbooks>
    <app-useful></app-useful>
  </template>
  
  <script>
  import AppBredcrumbs from '../components/AppBredcrumbs';
  import AppBlbooks from '../components/services-page/AppBlbooks';
  import AppUseful from '../components/AppUseful';
  export default {
        mounted(){
            this.$store.getters.scrollPageToTop;
            this.$store.commit('changeAmountBredcrumbs', {value : 2});
            this.$store.commit('changeBredcrumbs', {values: ['Услуги', 'Родословные книги'], paths: ['/services', '/services/books']});
        },
        unmounted(){
            this.$store.commit('hideBredcrumb')
        },        
      components: { AppBredcrumbs, AppBlbooks, AppUseful}
  }
  </script>
  
