<template>
  <section class="sharebtn">
        <div class="container">
            <div class="sharebtn__inner">
				<div class="sharebtn__box"
					:class="{'active' : shareOpened}"
					@mouseleave="closeLinks">				
					<div class="sharebtn__links">
						<a :href="`https://vk.com/share.php?url=${myUrl}`" class="sharebtn__link">
							<img src="../../assets/img/share_vk.svg" alt="../../assets/img/share_vk.svg">
						</a>
						<a :href="`https://t.me/share/url?url=${myUrl}`"  class="sharebtn__link">
							<img src="../../assets/img/share_tg.svg" alt="../../assets/img/share_tg.svg">
						</a>
						<a :href="`https://api.whatsapp.com/send?text=${myUrl}`" class="sharebtn__link">
							<img src="../../assets/img/share_twitter.svg" alt="../../assets/img/share_twitter.svg">
						</a>
					</div>
					<div class="sharebtn__copy"
						@click="copyURL(myUrl)">
						Скопировать ссылку
					</div>
				</div>
                <button class="sharebtn__self"
						@click="openLinks">
					<div class="sharebtn__img"
						:class="{'active' : shareOpened}">
						<img src="../../assets/img/share_btn.svg" alt="../../assets/img/share_btn.svg">
					</div>
					<div class="sharebtn__text">
						Поделиться
					</div>
				</button>
            </div>
        </div>
    </section>
</template>

<script>
export default {
	data() {
		return {
			shareOpened: false,
			myUrl: "www.bloodline.ru/knowledge_base/news"
		}
	},
	methods: {
		openLinks() {
			this.shareOpened = !this.shareOpened
		},
		closeLinks() {
			this.shareOpened = false
		},
		async copyURL(mytext) {
			try {
			await navigator.clipboard.writeText(mytext);
			alert('Ссылка скопирована!');
			} catch(e) {
			alert('Ошибка копирования ссылки(((');
			}
			this.shareOpened = !this.shareOpened
		}
	},
	unmounted() {
		this.shareOpened = false
	}
}
</script>

<style scoped>
.sharebtn {
	margin-top: 30px;
}
.sharebtn__inner{	
	position: relative;
}
.sharebtn__box{
	width: 170px;
	height: 91px;
	max-width: 170px;
	max-height: 91px;
	box-sizing: border-box;
	padding-top: 12px;
	background: #f5f5f5;
	border-radius: 20px;

	position: absolute;
	top: -112px;
	left: 0;
	display: none;
}
.sharebtn__box.active{
	display: block;
}
.sharebtn__links{
	display: flex;
	justify-content: center;
	align-items: center;
	column-gap: 10px;
}
.sharebtn__link{
	width: 34px;
	height: 34px;
	cursor: pointer;
}
.sharebtn__copy{
	margin-top: 2px;
	cursor: pointer;

	font-weight: 500;
	font-size: 14px;
	line-height: 33px;
	text-decoration: underline;
	color: #1D1D1D;
	text-align: center;
}
.sharebtn__self{
	width: fit-content;
	border: medium none;
	background-color: transparent;
}
.sharebtn__self:hover{
	cursor: pointer;
}
.sharebtn__img{
	max-width: 39px;
	max-height: 39px;
	display: inline-block;
	vertical-align: middle;
}
.sharebtn__img{
	background: rgba(0, 0, 0, 0.28);
	border-radius: 10px;
}
.sharebtn__img.active{
	background: #007632;
}
.sharebtn__text{
	display: inline-block;
	margin-left: 20px;
	font-style: normal;
	font-weight: 500;
	font-size: 18px;
	line-height: 33px;
	color: #1D1D1D;
}
@media (max-width: 1024px) {
}
@media (max-width: 768px) {
}
@media (max-width: 480px) {
}
@media (max-width: 360px) {
}
</style>