<template>
	<section class="galleryalbum">
		<div class="container">
			<div class="galleryalbum__inner"
				:class="{'modalOpened' : modalIsOpen}">
				<h1 class="galleryalbum__title" v-if="!modalIsOpen">{{ getCurrentAlbum.title }}</h1>
				<div class="galleryalbum__photos" v-if="!modalIsOpen">
					<div class="galleryalbum__photo"
						v-for="(photoSrc, index) in getCurrentAlbum.photo"
						:key="index"
						@click="openModal()">
						<img loading="lazy" decoding="async" :src="photoSrc" alt="photo">
					</div>					
				</div>
				<div class="galleryalbum__modal"
					v-if="modalIsOpen">
					<div class="galleryalbum__btns">
						<div class="galleryalbum__btn"
							@click="zoom()">
							<img src="../../assets/img/gallery_zoom_btn.svg" alt="gallery_zoom_btn">
						</div>
						<div class="galleryalbum__btn"
							@click="closeModal()">
							<img src="../../assets/img/gallery_close_btn.svg" alt="gallery_zoom_btn">
						</div>
					</div>
					<div class="gallery__slider">
						<button type="button" class="slider_prev-btn prev disabled"></button>
						<div class="slider-window">
							<div class="slider-list">
								<div class="slider-track">
									<div class="slide"
										v-for="(photoSrc, index) in getCurrentAlbum.photo"
										:key="index"
										@click="selectedPhotoNum = index">
										<div class="slider-img">
											<label>
												<input type="checkbox" :checked="photoIsZoomed">
												<img :src="photoSrc" alt="gallery-image" class="gallery-image">
											</label>
										</div>
									</div>                         
								</div>
							</div>                        
						</div>
						<button type="button" class="slider_next-btn next"></button>
					</div>
				</div>
			</div>
		</div>
	</section>
</template>

<script>
import { mapGetters } from 'vuex';
import {dragDropSlider} from '../../assets/js/slider.js'

export default {
	data() {
		return {
			modalIsOpen : false,
			photoIsZoomed: false,
			selectedPhotoNum: 0,
		}
	},
	computed: mapGetters(['getCurrentAlbum']),
	methods: {
		openModal () {
			this.modalIsOpen = true
		},
		closeModal () {
			this.modalIsOpen = false;
			this.photoIsZoomed = false;
		},
		zoom () {
			this.photoIsZoomed = true;
		},
	},
	updated() {
        try {
            dragDropSlider('.slider-window', '.slider-list', '.slider-track', '.slide', '.gallery__slider', this.getCurrentAlbum.photo.length);
        } catch (error) {console.log(error)}
    }
}
</script>

<style scoped>
.slider-img input[type=checkbox] {
  display: none
}
.slider-img img {
  transition: transform 0.25s ease;
  cursor: zoom-in
}
.slider-img input[type=checkbox]:checked~img {
  transform: scale(2);
  cursor: zoom-out
}









.galleryalbum__modal{
	position: absolute;
	top: 0;
	left: 0;
	background-color: #F5F5F5;
	width: 100%;
	height: 100%;
}
.galleryalbum__btns{
	position: absolute;
	top: 60px;
	right: 0;
	display: flex;
	justify-content: flex-end;
	align-items: center;
	column-gap: 34px;
}
.galleryalbum__btn{
	width: 32px;
	height: 32px;
	cursor: pointer;
}
.galleryalbum__btn:last-child{
	width: 21px;
	height: 21px;
}
.gallery__slider{
	margin: 0 auto;
	display: flex;
	justify-content: space-between;
	align-items: center;
	column-gap: 35px;
	height: 100%;
}
.slider_prev-btn, .slider_next-btn{
	background: inherit;
	border-radius: 50%;
	background-repeat: no-repeat;
	background-size: 120% 120%;
	background-position: center center;
	width: 60px;
	height: 60px;
}
.slider_prev-btn{
	background-image: url("../../assets/img/btn_slider_left.svg");
}
.slider_next-btn{
	background-image: url("../../assets/img/btn_slider_right.svg");
}
.slider_next-btn:hover{
	background-image: url("../../assets/img/btn_slider_right_hover.svg");
}
.slider_prev-btn:hover{
	background-image: url("../../assets/img/btn_slider_left-hover.svg");
}
.slider-window {
	vertical-align: middle;
	position: relative;
	display: inline-block;
	width: 100%;
	/* Чтобы во время перетаскивания слайда ничего не выделить внутри него */
	user-select: none;
	/* Чтобы запретить скролл страницы, если мы начали двигать слайдер по оси X */
	touch-action: pan-y;
}
/* Если где-то внутри слайдера будут изображения,
то нужно задать им pointer-events: none,
чтобы они не перетаскивались мышью */
/* .slider img {
	pointer-events: none;
}
.slider-img {
	pointer-events: none;
} */
.slider-list {
	width: 100%;
	height: 745px;
	overflow-x: hidden;
	-ms-overflow-style: none;
	scrollbar-width: none;
}
.slider-list::-webkit-scrollbar {
      width: 0;
      height: 0;
}
.slider-list.grab {
	cursor: grab;
}
.slider-list.grabbing{
	cursor: grabbing;
}
.slider-track {
	display: flex;
}
.slide {
	width: 100%;
	height: 745px;
	flex-shrink: 0;
	display: flex;
	align-items: center;
	justify-content: center;
	box-sizing: content-box;
}
.slider-img {
	height: 100%;
}
.slide img {
	width: 100%;
	height: 100%;
}
.slider-arrows {
	margin-top: 50px;
	text-align: center;
}  
.next,
.prev {
	display: inline-block;
	vertical-align: middle;
	padding: 0;
	border: none;
	cursor: pointer;
}
.next.disabled,
.prev.disabled {
	opacity: .25;
	pointer-events: none;
}


















.galleryalbum{
	margin: 5px 0 55px 0;
}
.galleryalbum__inner{
	position: relative;
}
.galleryalbum__inner.modalOpened{
	height: 850px;
	max-height: 850px;
}
.galleryalbum__title{
	font-family: 'Noto', serif;
	font-weight: 600;
	font-size: 32px;
	line-height: 44px;
	color: #7D4E24;
	margin-bottom: 25px;
}
.galleryalbum__photos{
	display: flex;
	flex-wrap: wrap;
	align-items: center;
	gap: 10px;
	justify-content: space-evenly;
	margin-bottom: 80px;
}
.galleryalbum__photo{
	width: fit-content;
	height: fit-content;
}

@media (max-width: 1024px) {
	.galleryalbum__title{
		font-size: 24px;
		line-height: 33px;
	}
	.galleryalbum__photo{
		flex: 0 0 30%;
	}
}
@media (max-width: 768px) {	
	
}
@media (max-width: 480px) {
	
}
@media (max-width: 360px) {
	
}
</style>