<template>
    <app-bredcrumbs></app-bredcrumbs>
    <app-photo-gallery></app-photo-gallery>
</template>
  
  <script>
  import AppBredcrumbs from '../components/AppBredcrumbs';
  import AppPhotoGallery from '../components/photo-gallery/AppPhotoGallery';
  export default {
        mounted(){
            this.$store.getters.scrollPageToTop;
            this.$store.commit('changeAmountBredcrumbs', {value : 2});       
            this.$store.commit('changeBredcrumbs', {values: ['О компании', 'Фотогалерея'], paths: ['/about_company', '/about_company/photo_gallery']});
        },
        unmounted(){
            this.$store.commit('hideBredcrumb')
        },        
      components: { AppBredcrumbs, AppPhotoGallery}
  }
  </script>
  
