<template>
  <app-bredcrumbs></app-bredcrumbs>
  <app-main-services></app-main-services>
  <app-beginning></app-beginning>
</template>

<script>
import AppBredcrumbs from '../components/AppBredcrumbs';
import AppMainServices from '../components/services-page/AppMainServices';
import AppBeginning from '../components/AppBeginning';
export default {
  mounted() {
    this.$store.getters.scrollPageToTop;
    this.$store.commit('changeAmountBredcrumbs', {value : 1});
    this.$store.commit('changeBredcrumbs', {values: ['Услуги'], paths: ['/services']});
  },
  components: { AppBredcrumbs, AppMainServices, AppBeginning}
}
</script>

