<template>
  <section class="news">
        <div class="container">
            <div class="news__inner">
                <h1 class="news__title">
					{{ getCurrentArticle.title }}
                </h1>
				<div class="news__image">
					<img :src="getCurrentArticle.src" alt="news__image">
				</div>
				<div class="news__desc">
					{{ getCurrentArticle.desc }}
				</div>
            </div>
        </div>
    </section>
</template>

<script>
import { mapGetters } from 'vuex';

export default {
	computed: mapGetters(["getCurrentArticle"]),
	mounted() {
		this.$store.commit('changeBredcrumbs', {values: ['Статьи', this.getCurrentArticle.title], paths: ['/articles', this.getCurrentArticle.url]});
	},
	updated() {
		this.$store.commit('changeBredcrumbs', {values: ['Статьи', this.getCurrentArticle.title], paths: ['/articles', this.getCurrentArticle.url]});
		this.$store.getters.scrollPageToTop;
	}
}
</script>

<style scoped>
.news{
	margin: 5px 0 30px 0;
}
.news__title{
	font-family: 'Noto Serif';
	font-style: normal;
	font-weight: 600;
	font-size: 32px;
	line-height: 44px;
	color: #7D4E24;

	margin-bottom: 30px;
}
.news__image{
	max-width: 580px;
	max-height: 303px;
	border-radius: 30px;
}
.news__desc{
	font-style: normal;
	font-weight: 400;
	font-size: 20px;
	line-height: 24px;
	color: rgba(0, 0, 0, 0.72);

	margin-top: 30px;
	max-width: 780px;
}
