<template>
    <app-bredcrumbs></app-bredcrumbs>
    <app-history-corp></app-history-corp>
    <app-working-map></app-working-map>
    <app-why-we></app-why-we>
    <app-beginning></app-beginning>
</template>

<script>
import AppBredcrumbs from '../components/AppBredcrumbs';
import AppHistoryCorp from '../components/company-history/AppHistoryCorp';
import AppWorkingMap from '../components/company-history/AppWorkingMap';
import AppWhyWe from '../components/company-history/AppWhyWe';
import AppBeginning from '../components/AppBeginning';
export default {
    mounted() {
        this.$store.getters.scrollPageToTop;
        this.$store.commit('changeAmountBredcrumbs', {value : 2});
        this.$store.commit('changeBredcrumbs', {values: ['О компании', 'История компании'], paths: ['/about_company', '/about_company/company_history']});
    },
    unmounted(){
        this.$store.commit('hideBredcrumb');
        this.$store.commit('changeBredcrumbs', {values: [''], paths: ['']});
    }, 
    components: { AppBredcrumbs, AppHistoryCorp, AppWorkingMap, AppWhyWe, AppBeginning }
}
</script>
    
