<template>
  <section class="might-like">
        <div class="container">
            <div class="might-like__inner">
				<div class="might-like__title">
					Вам также может понравиться:
				</div>
                <div class="might-like__cards">
					<router-link :to = "this.cards[this.cardsNum[0]].url"
						@click="$store.commit('setCurrentArticle', {value : cardsNum[0]}) , getRandomKnowledgeCards()"
						class="might-like__card">
						<div class="might-like__img">
							<img :src="this.cards[this.cardsNum[0]].src" alt="might-like__img">
						</div>
						<div class="might-like__text">
							{{this.cards[this.cardsNum[0]].title}}
						</div>
					</router-link>
					<router-link :to = "this.cards[this.cardsNum[1]].url"
						@click="$store.commit('setCurrentArticle', {value : cardsNum[1]}) , getRandomKnowledgeCards()"
						class="might-like__card">
						<div class="might-like__img">
							<img :src="this.cards[this.cardsNum[1]].src" alt="might-like__img">
						</div>
						<div class="might-like__text">
							{{this.cards[this.cardsNum[1]].title}}
						</div>
					</router-link>
				</div>
            </div>
        </div>
    </section>
</template>

<script>
export default {
	data() {
		return {
			cardsNum : [],
			cards: this.$store.getters.getAllArticles
		}
	},
	methods: {
		getRandomKnowledgeCards() {   
			let lengthArr = this.cards.length;
			let rndNum1 = Math.floor(Math.random() * lengthArr);
			let rndNum2;
			if (this.cards.length > 1) {
				do {
					rndNum2 = Math.floor(Math.random() * lengthArr);
				} while(rndNum1 == rndNum2);
			}
            this.cardsNum = [rndNum1,rndNum2];
        },
	},
	beforeMount() {
		this.cardsNum = this.$store.getters.getRandomKnowledgeCards;
		this.getRandomKnowledgeCards();
	},
}
</script>

<style scoped>
.might-like {
	margin: 40px 0 80px 0;
}
.might-like__title{
	font-weight: 700;
	font-size: 20px;
	line-height: 30px;
	color: #1D1D1D;

	margin-bottom: 30px;
}
.might-like__cards{
	display: flex;
	column-gap: 31px;
}
.might-like__card{
	width: 330px;
	max-width: 330px;
	text-decoration: none;
}
.might-like__img{
	max-width: 100%;
	max-height: 172px;
	border-radius: 10px;
}
.might-like__text{
	font-weight: 500;
	font-size: 20px;
	line-height: 24px;
	color: rgba(0, 0, 0, 0.72);
	margin-top: 20px;
}






