import { createStore, createLogger } from "vuex";
import bredcrumbsModule from './modules/bredcrumbs';
import requestCallModule from './modules/requestCall';
import articlesModule from './modules/articles';
import photoGallery from './modules/photo-gallery';

export default createStore({
    modules: {
        articles: articlesModule,
        bredcrumbs: bredcrumbsModule,
        photoGallery: photoGallery,
        requestCall: requestCallModule,        
    },
    plugins: [createLogger()],
    getters: {
        scrollPageToTop(){
            // Scroll to top for all views
            return window.scrollBy({
                        top: -Math.max( document.body.scrollHeight, document.body.offsetHeight, 
                            document.documentElement.clientHeight, document.documentElement.scrollHeight, document.documentElement.offsetHeight ),
                        behavior: 'smooth'
                    })
        }
    }
})