<template>
    <app-bredcrumbs></app-bredcrumbs>
    <app-our-progress></app-our-progress>
</template>
  
  <script>
  import AppBredcrumbs from '../components/AppBredcrumbs';
  import AppOurProgress from '../components/progress/AppOurProgress';
  export default {
        mounted(){
            this.$store.getters.scrollPageToTop;
            this.$store.commit('changeAmountBredcrumbs', {value : 2});
            this.$store.commit('changeBredcrumbs', {values: ['О компании', 'Наши достижения'], paths: ['/about_company', '/about_company/progress']});
        },
        unmounted(){
            this.$store.commit('hideBredcrumb')
        },        
      components: { AppBredcrumbs, AppOurProgress}
  }
  </script>
  
