<template>
  <app-bredcrumbs></app-bredcrumbs>
  <app-military-search></app-military-search>
</template>
  
  <script>
import AppBredcrumbs from "../components/AppBredcrumbs";
import AppMilitarySearch from "../components/services-page/AppMilitarySearch";
export default {
  mounted() {
    this.$store.getters.scrollPageToTop;
    this.$store.commit('changeAmountBredcrumbs', {value : 2});
    this.$store.commit('changeBredcrumbs', {values: ['Услуги', 'Военный поиск'], paths: ['/services', '/services/military_search']});
  },
  unmounted() {
    this.$store.commit("hideBredcrumb");
  },
  components: { AppBredcrumbs, AppMilitarySearch },
};
</script>
  
