<template>
    <app-bredcrumbs></app-bredcrumbs>
    <app-family-trees></app-family-trees>
  </template>
  
  <script>
  import AppBredcrumbs from '../components/AppBredcrumbs';
  import AppFamilyTrees from '../components/services-page/AppFamilyTrees';
  export default {
        mounted(){
            this.$store.getters.scrollPageToTop;
            this.$store.commit('changeAmountBredcrumbs', {value : 2});
            this.$store.commit('changeBredcrumbs', {values: ['Услуги', 'Родословные древа'], paths: ['/services', '/services/family_trees']});
        },
        unmounted(){
            this.$store.commit('hideBredcrumb')
        },        
      components: { AppBredcrumbs, AppFamilyTrees}
  }
  </script>
  
